.lSide {
   grid-area: Lside;
   background: #EDEDED;
   height: 100px;
   padding: 32px;
   border-radius: 5px;
   height: auto;
}

.title {
   font-family: Roboto;
   font-size: 20px;
   font-style: normal;
   font-weight: 700;
   line-height: 23px;
   letter-spacing: 0em;
   text-align: left;   
   margin-bottom: 30px;
}

.addDo {
   background: #FFFFFF;
   border-radius: 5px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 18px 27px;
}

.addDo input {
   border: none;
   font-family: Roboto;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 19px;
   letter-spacing: 0em;
   text-align: left;
}

.addDo button {
   border: none;
   background: #00B3DB;
   height: 41px;
   width: 46px;
   border-radius: 5px;
   font-family: Roboto;
   font-style: normal;
   letter-spacing: 0em;
   color: #FFFFFF;
   cursor: pointer;
}