.rSide {
   grid-area: Rside;
   background: #EDEDED;
   padding: 38px 48px;
   border-radius: 5px;

}

.title {
   display: flex;
   align-items: center;
   justify-content: space-between;
   margin-bottom: 25px;
}

.title h2 {
   font-family: Roboto;
   font-size: 20px;
   font-style: normal;
   font-weight: 700;
   line-height: 23px;
   letter-spacing: 0em;
   text-align: left;
}

.title input {
   font-family: Roboto;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 19px;
   letter-spacing: 0em;
   text-align: left;
   padding: 12px 27px;
   border: none;
   border-radius: 5px;

}

.link a {
   list-style-type: none;
   text-decoration: none; 
   font-family: Roboto;
   font-size: 15px;
   font-style: normal;
   font-weight: 700;
   line-height: 25px;
   letter-spacing: 0em;
   color: #000000;
}

.link a:focus {
   color: #00B3DB;
}

.link a:nth-child(2)::after {
   content: " / ";
}

.link a:nth-child(2)::before {
   content: " / ";
}