.title {
    margin-top: 60px;
    margin-bottom: 52px;
}

.title h2 {
    font-family: Roboto;
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: 53px;
    letter-spacing: 0em;
}

.bodyDo {
    display: grid;
    grid-template-columns: 0.7fr 1.3fr;
    grid-template-rows: 1fr;
    grid-template-areas: "Lside Rside";
    grid-column-gap: 33px;
}