.body {
   padding: 27px 32px;
   background-color: #FFFFFF;
   border-radius: 5px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   margin-top: 22px;
   margin-bottom: 8px;
}

.name {
   font-family: Roboto;
   font-size: 20px;
   font-style: normal;
   font-weight: 700;
   line-height: 23px;
   letter-spacing: 0em;
   color: #000000;
}

.switch {
   position: relative;
   display: inline-block;
   width: 60px;
   height: 34px;
 }
 
 .switch input {
    display:none;
}
 
 .slider {
   position: absolute;
   cursor: pointer;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background-color: #ccc;
   -webkit-transition: .4s;
   transition: .4s;
 }
 
 .slider:before {
   position: absolute;
   content: "";
   height: 26px;
   width: 26px;
   left: 4px;
   bottom: 4px;
   background-color: white;
   -webkit-transition: .4s;
   transition: .4s;
 }
 
 input:checked + .slider {
   background-color: #2196F3;
 }
 
 input:focus + .slider {
   box-shadow: 0 0 1px #2196F3;
 }
 
 input:checked + .slider:before {
   -webkit-transform: translateX(26px);
   -ms-transform: translateX(26px);
   transform: translateX(26px);
 }
 
 .slider.round {
   border-radius: 34px;
 }
 
 .slider.round:before {
   border-radius: 50%;
}